import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-list-employee',
  templateUrl: './list-employee.component.html',
  styleUrls: ['./list-employee.component.scss']
})
export class ListEmployeeComponent implements OnInit {
  @Input("visibleEmployeeList") visibleEmployeeList: boolean = false;
  @Output("closeAndReceiveEmployeeList") closeAndReceiveEmployeeList = new EventEmitter<any>()
  listEmployee: Array<any> = this.vhAuth.getlocalEmployees()
  listEmployeeShow: Array<any> = []
  constructor(
    private vhAuth: VhAuth,
    private vhAlgorithm: VhAlgorithm,
  ) { }

  ngOnInit(): void {
    this.listEmployeeShow = this.listEmployee.map(item => {
      return {
        ...item,
        checked: false
      }
    })
    this.listEmployee = { ...this.listEmployeeShow }
  }
  goBack() {
    this.closeAndReceiveEmployeeList.emit(false)
  }
  tempVal: string = ''
  searchEmployee(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length) this.listEmployeeShow = this.vhAlgorithm.searchList(tempVal, this.listEmployee, ['name'])
    else this.listEmployeeShow = this.listEmployee
  }
  checkedAll: boolean = false;
  selectAll(event) {
    this.listEmployeeShow.forEach(item => item.checked = this.checkedAll)
  }
}
