import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-paybill',
  templateUrl: './paybill.component.html',
  styleUrls: ['./paybill.component.scss']
})
export class PaybillComponent implements OnInit {
  @Input("visiblePaybill") visiblePaybill: boolean = false;
  @Output("closeAndReceivePaybill") closeAndReceivePaybill = new EventEmitter<any>()
  constructor() { }

  ngOnInit(): void {
  }
  goBack() {
    this.closeAndReceivePaybill.emit(false)
  }
}
