import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PayOffComponent } from './pay-off.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { ListEmployeeComponent } from './list-employee/list-employee.component';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { PaybillComponent } from './paybill/paybill.component';
const routes: Routes = [
  {
    path: '',
    component: PayOffComponent
  },
  {
    path: 'add',
    component: AddComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  },
]

@NgModule({
  declarations: [PayOffComponent, AddComponent, DetailComponent, ListEmployeeComponent, PaybillComponent],
  exports: [ListEmployeeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule.withConfig({ warnOnNgModelWithFormControl: 'never' }),
    TranslateModule,
    ScrollingModule
  ]
})
export class PayOffModule { }
